        <site-menu v-if="!isMobile" :currentPage="page"></site-menu>
      </div>
      <div class="col-right">
        <search-bar></search-bar>
        <button class="gnb-open" v-if="isMobile">
          <span class="blind">대메뉴 열기</span>
